
    export default {
        name: 'AddNote',
        data() {
            return {
                text: '',
                day: '',
                reminder: false
            }
        },
        methods: {
            onSubmit(e) {
                e.preventDefault()

                if(!this.text) {
                    alert('Please add a note')
                    return
                }

                const newNote = {
                    text: this.text,
                    day: this.day,
                    reminder: this.reminder
                }

                this.$emit('add-note', newNote)

                this.text = ''
                this.day = ''
                this.reminder = false
            }
        }
    }
