
import Button from './Button'

export default {
  name: 'Header',
  props: {
      title: String,
      showAddNote: Boolean
  },
  components : {
      Button
  },
  computed: {
    homePage() {
      if(this.$route.path === '/') {
        return true
      } else {
        return false
      }
    }
  }
}
